:root {
    --primary-color: rgb(255,255,255);
    --secondary-color: rgba(254,254,0);
    --contrast-color: rgb(254,0,246);
    --bg-color: rgb(0,0,0);
  }
  
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  
  /** GOOGLE FONTS**/
  /* Fuente cabecera */
  .jersey-20-regular {
    font-family: "Jersey 20", sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  /* Fuente Títulos */
  .righteous-regular {
    font-family: "Righteous", sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  /* Fuente Textos */
  .oswald-extralight {
    font-family: "Oswald", sans-serif;
    font-optical-sizing: auto;
    font-weight: 200;
    font-style: normal;
  }
  
  .oswald-light {
    font-family: "Oswald", sans-serif;
    font-optical-sizing: auto;
    font-weight: 300;
    font-style: normal;
  }
  
  .oswald-regular {
    font-family: "Oswald", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
  }
  
  
  main {
    background-color: var(--bg-color);
  }
  
  
  /** CONTENEDORES **/
  .header-homepage-container {
    height: 200px;
    padding: 30px;
    background-color: var(--bg-color);
  }
  
  .header-inner-container {
    height: 125px;
    padding: 30px;
    background-color: var(--bg-color);
  }
  
  .intro-container, .discography-container, .detail-container, .presentation-container, .sources-container {
    padding-top: 100px;
  }
  
  .sections-grid-container {
    margin: 50px 50px 0 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
    align-items: center;
  }
  
  .sections-grid-container figure img {
    display: block;
    width: 100%;
    margin: auto;
  }
  
  .section-card {
    border: .1rem solid;
    border-color: var(--primary-color);
    justify-content: space-between;
  }
  
  figcaption {
    background-color: var(--primary-color);
  }
  
  figcaption p {
    padding: 30px;
    text-align: center;
    color: var(--contrast-color);
    font-family: "Oswald", sans-serif;
    font-weight: 300;
  }
  
  figcaption p a {
    color: var(--contrast-color);
    font-family: "Oswald", sans-serif;
    font-weight: 300;
    text-decoration: none;
  }
  
  figcaption p a:hover {
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    text-decoration: underline;
  }
  
  .cd-grid-container {
    padding: 100px 300px 0 300px;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    width: 100%;
    text-align: center;
  }
  
  .cd-grid-container figure {
    margin: auto;
    padding: 50px;
  }
  
  .cd-card {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: var(--bg-color);
    justify-content: space-between;
  }
  
  .cd-card img {
    border: .1rem solid;
    border-color: var(--primary-color);
  }
  
  .cd-card img a:hover {
    border-color: var(--secondary-color);
  }
  
  .cd-card figcaption {
    background-color: var(--bg-color);
  }
  
  .cd-card figcaption p {
    padding: 20px 0 0 20px;
    color: var(--primary-color);
  }
  
  .cd-card figcaption p a {
    color: var(--secondary-color);
  }
  
  
  /** TEXTO **/
  h1 {
    font-family: "Jersey 20", sans-serif;
    font-size: 5rem;
    text-align: center;
    color: var(--primary-color);
  }
  
  h1 a {
    color: var(--primary-color);
    text-decoration: none;
  }
  
  .header-subtitle {
    font-family: "Oswald", sans-serif;
    font-size: 1rem;
    text-align: center;
    color: var(--secondary-color);
  }
  
  h2 {
    font-family: "Righteous", sans-serif;
    font-size: 2rem;
    text-align: center;
    color: var(--primary-color);
  }
  
  h3 {
    font-family: "Righteous", sans-serif;
    font-size: 1.5rem;
    text-align: center;
    color: var(--primary-color);
  }
  
  p {
    padding: 30px 75px 10px 75px;
    font-family: "Oswald", sans-serif;
    font-weight: 300;
    font-size: 1.2rem;
    text-align: left;
    color: var(--primary-color);
  }
  
  .opus-name {
    font-size: 2rem;
    text-align: left;
  }
  
  .opus-year {
    font-size: 1rem;
    text-align: left;
  }
  
  .opus-songs {
    font-size: 1rem;
    text-align: left;
  }
  
  .opus-detail {
    font-weight: 400;
    color: var(--secondary-color);
    text-align: center;
  }
  
  .opus-songs {
    padding-top: 100px;
  }
  
  .opus-songs-list {
    padding: 20px;
    background-color: var(--primary-color);
    color: var(--contrast-color);
    font-family: "Oswald", sans-serif;
    font-weight: 300;
    font-size: 1.5rem;
    text-align: center;
    list-style-type: none;
  }
  
  .sources h3 {
    padding-top: 30px;
    font-family: "Righteous", sans-serif;
    font-size: 1.5rem;
  }
  
  .sources p {
    padding-top: 10px;
    font-family: "Oswald", sans-serif;
    font-weight: 200;
    text-align: center;
  }
  
  .sources ul {
    color: var(--primary-color);
    text-align: center;
    list-style-type: none;
    }
  
  .sources ul li {
      padding: 10px;
      font-family: "Oswald", sans-serif;
      font-weight: 200;
      }
  
  .sources a {
    color: var(--secondary-color);
  }
  
  .sources a:hover {
    color: var(--contrast-color);
  }
  
  
  /** BREADCRUMB **/
  .breadcrumb-container {
    background-color: var(--bg-color);
    font-family: "Oswald", sans-serif;
    font-weight: 200;
    text-align: center;
  }
  
  ul.breadcrumb {
    padding: 30px;
    list-style: none;
    font-size: .9rem;
  }
  ul.breadcrumb li {
    display: inline-block;
  }
  
  ul.breadcrumb li + li {
    padding-left: .25rem;
  }
  
  ul.breadcrumb li + li::before {
    padding-right: .25rem;
    content: ">";
    color: var(--secondary-color);
  }
  
  ul.breadcrumb li a {
    color: var(--primary-color);
    text-decoration: none;
  }
  
  ul.breadcrumb li a:hover {
    font-weight: 400;
    text-decoration: none;
  }
  
  ul.breadcrumb li.active  {
    color: var(--secondary-color);
    text-decoration: underline;
  }
  
  
  /** IMÁGENES **/
  .header-image {
    display: flex;
    justify-content: center;
  }
  
  .detail-cover {
    padding: 50px;
    display: flex;
    justify-content: center;
  }
  
  .detail-cover figure {
    border: .1rem solid;
    border-color: var(--primary-color);
  }
  
  .detail-back-cookie {
    display: flex;
    justify-content: center;
  }
  
  .detail-back-cookie figure {
    border: .1rem solid;
    border-color: var(--primary-color);
  }
  
  .slideshow {
    padding: 100px 50px 100px 50px;
    display: flex;
    justify-content: center;
  }
  
  
  /** FOOTER **/
  .footer {
    background-color: var(--bg-color);
    padding: 50px 100px 50px 100px;
  }
  
  .footer-container {
    font-family: "Oswald", sans-serif;
    font-weight: 200;
    color: var(--primary-color);
  }
  
  .footer-container a {
    color: var(--secondary-color);
    text-decoration: none;
  }
  
  .footer-container a:hover {
    color: var(--secondary-color);
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    text-decoration: underline;
  }
  
  .copyrights {
    padding-bottom: 20px;
  }
  
  .footer ul li {
    list-style-type: none; 
  }
  
  .data {
    padding: 0;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 1rem;
    text-align: left;
  }
  
  .data p {
    padding: 0;
  }
  
  .credit {
    background-color: var(--bg-color);
    text-align: center;
  }
  
  .credit cite {
    font-size: 1rem;
    text-transform: uppercase;
  }
  
  
  /** NAVEGACIÓN **/
  nav {
    position: sticky;
    top: 0;
    background: var(--bg-color);
    padding: 1.4rem 0;
  }
  
  nav ul { 
    display: flex;
    padding: 0 50px 0 50px;
    justify-content: space-between;
    text-transform: uppercase;
    font-size: .9rem;
    list-style-type: none;
  }
  
  nav li {
    padding-bottom: .5rem;
    font-family: "Oswald", sans-serif;
    font-weight: 300;
    text-align: center;
  }
  
  nav li a {
    color: var(--primary-color);
    text-decoration: none;
  }
  
  nav li a:hover {
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    text-decoration: none;
  }
  
  nav li.active {
    color: var(--secondary-color);
    text-decoration: underline;
  }
  
  
  /** REPRODUCTOR SPOTIFY **/
  .spotify {
    padding: 50px;
    display: flex;
    justify-content: center;
  }
  
  
  /** CITAS **/
  .quotes {
    padding: 100px 200px 50px 200px;
  }
  
  blockquote p {
    font-family: "Righteous", sans-serif;
    font-weight: 400;
    font-size: 1.3rem;
    margin-bottom: 2rem;
  }
  
  q {
    font-style: italic;
    quotes: "\00AB" "\00BB"
  }
  
  cite {
    color: var(--secondary-color);
    font-family: "Oswald", sans-serif;
    font-weight: 200;
    font-size: 1.3rem;
    font-style: normal;
  }
  
  
  /** VÍDEOS **/
  .presentation-video {
    display: flex;
    justify-content: center;
  }
  
  iframe {
    border: 0;
    text-align: center;
  }